body{width:100vw;height:100vh;margin:0;padding:0;color:#333;background:url("../img/00-template.svg") center no-repeat;background-size:contain;font-family:sans-serif;color:rgba(0,0,0,0.7)}a{text-decoration:none;color:#3a88e0;}a:hover{text-decoration:underline}.slides{width:100vw;height:100vh;position:relative;overflow:hidden}.slide{width:100vw;height:100vh;top:0;left:0;background-size:contain;background-position:center;background-repeat:no-repeat;position:absolute;transition:top .5s ease-in-out}.current{top:0}.next{top:100vh}.previous{top:-100vh}.notes{position:absolute;top:0;right:0;height:100vh;background:rgba(255,255,255,0.8);box-shadow:-1px 0 3px rgba(0,0,0,0.3);box-sizing:border-box;padding:20px;min-width:30vw;display:none;}.notes.open{display:block}